.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#floatingTextarea2{
  height:250px;
}

.copy{
  color:white;
  font-size:2em;
}
.contact{
  color:rgb(185, 185, 185);
  font-size:1em;
}
.right{
  color:rgb(212, 211, 211);
  font-size:0.5em;
}
.imgS{
  border-radius: 50%;
}

.footerS{
  margin-top:100px;
}
.main{
  height:70vh
}

